import React, { useEffect, useRef, memo } from 'react';
import { useMountedState } from 'react-use';
import { PanZoom } from 'react-easy-panzoom';
import Button from '../UI/Button/Button';
import cls from './PanZoomComponent.module.scss';
import classNames from 'classnames';
const PanZoomComponent = memo(
	props => {
		const { children, zoomStep = 10, controlsContainerClass, panZoomProps, noControls = false } = props;
		const isMounted = useMountedState();
		const contentRef = useRef();

		useEffect(() => {
			if (!isMounted) contentRef.current = null;
			return () => {
				contentRef.current = null;
			};
		}, []);
		const renderControls = () => {
			return (
				<div className={classNames(cls.controlsContainer, controlsContainerClass)}>
					Click to Edit
					<Button
						title={'+'}
						clicked={() => {
							contentRef.current.zoomIn(zoomStep);
						}}
						className={cls.controlsButton}
					/>
					<Button
						title={'◯'}
						clicked={() => {
							contentRef.current.reset();
						}}
						className={cls.controlsButton}
					/>
					<Button
						title={'-'}
						clicked={() => {
							contentRef.current.zoomOut(zoomStep);
						}}
						className={cls.controlsButton}
					/>
				</div>
			);
		};
		const defaultConfig = {
			autoCenter: true, //Auto-center the view when mounting . default=false
			autoCenterZoomLevel: 1, //number		Specify the initial zoom level for auto-center
			zoomSpeed: 1, //number	1	Sets the zoom speed
			doubleZoomSpeed: 1.75, //	number	1.75	Sets the zoom speed for double click
			disabled: false, //bool	false	Disable pan and zoom
			disableKeyInteraction: false, //bool	false	Disable keyboard interaction
			disableDoubleClickZoom: false, //bool	false	Disable zoom when performing a double click
			disableScrollZoom: false, //bool	false	Disable zoom when performing a scroll
			realPinch: false, //bool	false	Enable real pinch interaction for touch events
			//keyMapping	//object	false	Define specific key mapping for keyboard interaction (e.g. { '<keyCode>': { x: 0, y: 1, z: 0 } }, with <keyCode> being the key code to map)
			// minZoom:0.5,	//number		Sets the minimum zoom value
			// maxZoom:1.5,	//number		Sets the maximum zoom value
			enableBoundingBox: true, //boolean	false	Enable bounding box for the panzoom element. The bounding box will contain the element based on a ratio of its size
			boundaryRatioVertical: 0.8, //number	0.8	Vertical ratio for the bounding box
			boundaryRatioHorizontal: 0.8, //number	0.8	Horizontal ratio for the bounding box
			noStateUpdate: true, //bool	true	Disable state update for each new x, y, z transform value while panning. Enabling it drastically increases the performances
			onPanStart: event => {
				console.log('onPanStart', event);
			}, //func Fired on pan start
			onPan: event => {
				console.log('onPan', event);
			}, //func Fired on pan
			onPanEnd: event => {
				console.log('onPanEnd', event);
			}, // func Fired on pan end
			//preventPan:preventPan, //func Defines a function to prevent pan
			style: {}, //object Override the inline-styles of the root element
			onStateChange: event => {
				console.log('onStateChanged', event);
			}, //func Called after the state of the component has changed
		};
		const configs = { ...defaultConfig, ...panZoomProps };
		return (
			<div className={cls.host}>
				{noControls && renderControls()}
				<PanZoom ref={contentRef} {...configs}>
					{children}
				</PanZoom>
			</div>
		);
	},
	(prev, next) => prev.props === next.props
);
export default PanZoomComponent;
